.input {
    font-size: 1.1em;
    padding: 7px 7px 7px 0;
    border: none;
    border-bottom: 2px solid #ddd;
    outline: none;
    transition: border-bottom-color .2s ease-out,
        color .1s linear;
    color: #999;
    width: 100%;
}

.input:focus {
    border-bottom-color: #84c4ff;
}
.simple {
    font: small-caps bold 1em 'Trebuchet MS', Tahoma, sans-serif;
    color: #84c4ff;
    background-color: #f5f5f5;
    border: none;
    outline: none;
    border-radius: 5px;
    padding: 7px 10px;
    border: 1px solid transparent;
}
.simple:active {
    opacity: .8;
    border-color: #84c4ff;
}

.edit, .delete,
.save, .cancel,
.bookmark {
    width: 20px;
    height: 20px;
    border: none;
    outline: none;
    border-radius: 5px;
    cursor: pointer;
    background: #fff;
    background-position: center center;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-color: #fff;
}

.delete   { background-image: url(data:image/png;base64,)   }
.edit     { background-image: url(data:image/png;base64,)   }
.save     { background-image: url(data:image/png;base64,) }
.cancel   { background-image: url(data:image/png;base64,)   }
.add-todo {
    display: flex;
    font-family: 'Century Gothic', 'Gill Sans', sans-serif;
}

.add-todo__input {
    font-size: 1.1em;
    padding-right: 10px;
    width: 80%;
}

.add-todo__button {
    width: 20%;
    border-bottom-left-radius: 0;
    background: #84c4ff;
    color: #fff;
}
.status-bar__button {
    width: calc(100% / 3);
    transition: all .2s ease-out;
}
.status-bar__button:disabled {
    opacity: .7;
    box-shadow: 0 2px 7px 1px  #ddd;
    border-color: #84c4ff;
}
.status-bar__button:nth-child(2) {
    margin: 0 8px;
}
.switchbox-container {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.switchbox-container_checked .switchbox {
    background: #84c4ff;
}
.switchbox-container_checked .switchbox__thumb {
    background: #fff;
    left: calc(70% - 4px);
}
.switchbox__label {
    width: calc(100% - 60px);
}
.switchbox {
    position: relative;
    margin-left: auto;
    width: 40px;
    height: 20px;
    background: #f1f1f1;
    border-radius: 5px;
    transition: all .2s ease-out;
}

.switchbox__thumb {
    position: absolute;
    width: 30%;
    background: #84c4ff;
    height: 12px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    border-radius: 4px;
    left: 4px;
    transition: all .2s ease-out;
}
.bookmark { background-image: url(data:image/png;base64,)     }
.bookmark_active {
    background-image: url(data:image/png;base64,);
}
.confirm {
    background: #fff;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

.confirm__question {
    color: #84c4ff;
}

.confirm__button-group {
    margin-top: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
}
.confirm__yes,
.confirm__no {
    width: 25%;
    margin-left: 5px;
    margin-right: 5px;
    color: #fff;
}

.confirm__yes { background: #84c4ff }
.confirm__no { background: #ffb1b1 }
.toggler-display-content {
    border: none;
    outline: none;
    background: #f6f6f6;
    border: 1px solid #f3f3f3;
    color: #84c4ff;
    font: bold .8em 'Century Gothic', 'Gill Sans', sans-serif;
    width: 60px;
    border-radius: 5px;
    transition: border-color .2s ease-out;
}
.toggler-display-content:active {
    border-color: #84c4ff;
}
.todo {
    position: relative;
    border-radius: 5px;
    padding: 8px 16px;
    word-wrap: break-word;
    box-shadow: 0 0 5px #ddd;
    transition: 
        border-color .2s ease-out;
    border: 1px solid transparent;
}
.todo__edit-field {
    width: 100%;
    border: none;
    resize: none;
    border-bottom: 2px solid #ddd;
    box-shadow: 0 0 7px #ddd;
    border-radius: 5px;
}
.todo__edit-field:focus {
    border-bottom-color: #84c4ff;
}
.todo__confirm {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
}
.tool-panel {
    position: absolute;
    right: 16px;
    top: 4px;
    z-index: 99;
}
.tool-panel__bookmark-button {
    margin-right: 7px;
}
.tool-panel__edit-button {
    margin-right: 5px;
}
.tool-panel .tool-panel__delete-button {
    margin-left: 5px;
}
.todo .toggler-display-content {
    position: absolute;
    right: 16px;
    bottom: 8px;
}
.todo_completed {
    border-color: #84c4ff;
}
.todo__headline {
    font: small-caps 1.3em 'Trebuchet MS', Tahoma, sans-serif;
    color: #999;
    margin-bottom: 16px;
}
.todo .switchbox__label,
.todo .todo__edit-field {
    font: 1em / 150% 'Trebuchet MS', Tahoma, sans-serif;
    color: #999;
}
.todo + .todo {
    margin-top: 15px;
}
.todo__date {
    margin-top: 15px;
    font-style: italic;
    font-weight: bold;
    color: #ddd;
    font-size: .8em; 
}

.scrollbar { 
    position: relative;
}
.scrollbar__content {
    overflow: hidden;
    padding: 4px;
}
.scrollbar_active .scrollbar__content {
    padding-right: 20px;
}

.slide-track {
    position: absolute;
    display: none;
    width: 10px;
    right: 0;
    top: 0;
    bottom: 0;
    background: #f1f1f1;
    border-radius: 5px;
}

.slide-track__thumb {
    position: absolute;
    top: 0;
    width: 10px;
    border-radius: 5px;
    background: #84c4ff;
}

.scrollbar_active .slide-track {
    display: block;
}
.todo-list {
    flex-direction: column;
    margin-top: 20px !important;
}
.todo-list__message {
    font: italic small-caps 1.3em 'Trebuchet MS', Tahoma, sans-serif;
    color: #ccc;
    padding: 7px 10px;
    margin-left: 10px;
}
.todo-app {
    box-shadow: 0 0 7px #ddd; 
    max-width: 450px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 16px 10px;
    border-radius: 5px;
    font-family: 'Century Gothic', 'Gill Sans', sans-serif;
    overflow: hidden;
}
.todo-app__item + .todo-app__item {
    display: flex;
    margin-top: 15px;
}
.todo-app__header {
    color: #fff;
    margin: -16px -10px;
    padding: 5px 7px;
    text-indent: 25px;
    background-color: #9ed0ff;
    font-weight: bold;
    font-size: 1.8em;
    -webkit-font-feature-settings: "c2sc";
            font-feature-settings: "c2sc";
    font-variant: small-caps;
    margin-bottom: 16px;
}
.app__todo-app {
    margin: 15px;
}
* {
    margin: 0;
    padding: 0;
}
